.container {
    min-width: 500px;
    margin: 10px;
}

.skeleton {
    opacity: 0.7;
    /* 배경과 어울리게 하기 위함*/
    animation: skeleton-loading 1s linear infinite alternate;
}

.skeleton-title {
    margin-left: 5px;
    margin-top: 7px;
    width: 3em;
    height: 1em;
}

.skeleton-graph {
    width: 460px;
    height: 300px;
    margin-bottom: .25rem;
    border-radius: .25rem;
}

.graph-title {
    margin-bottom: 10px;
    display: flex;
}

// .skeleton-text:last-child {
//     margin-bottom: 0;
//     width: 80%;
// }

@keyframes skeleton-loading {
    0% {
        background-color: hsl(200, 20%, 70%);
    }

    100% {
        background-color: hsl(200, 20%, 95%);
    }
}